import { useState } from "react";
import { CardElement, useStripe, useElements } from "@stripe/react-stripe-js";
import { axiosPost } from "@/services/axiosService";
import { useDispatch } from "react-redux";
import { userSlice } from "../../redux/User";

const CheckoutForm = ({
  customerId,
  subscriptionId,
  planCode,
  isExpired,
  price,
  createInvoice,
  addon,
  handleClose = () => {},
}) => {
  const stripe = useStripe();
  const elements = useElements();
  const dispatch = useDispatch();
  const [saving, setSaving] = useState(false);

  const CARD_OPTIONS: any = {
    disableLink: true,
    iconStyle: "solid",
    hidePostalCode: true,
    style: {
      base: {
        color: "#32325d",
        fontFamily: '"Helvetica Neue", Helvetica, sans-serif',
        fontSmoothing: "antialiased",
        fontSize: "13px",
        "::placeholder": {
          color: "#9ca3af",
        },
      },
      invalid: {
        color: "#fa755a",
        iconColor: "#fa755a",
      },
    },
  };

  const handleSubmit = async (event) => {
    event.preventDefault();
    setSaving(true);

    if (!stripe || !elements) {
      setSaving(false);
      return;
    }

    const cardElement = elements.getElement(CardElement);

    if (!cardElement) {
      setSaving(false);
      return;
    }

    const { error, paymentMethod } = await stripe.createPaymentMethod({
      type: "card",
      card: cardElement,
      billing_details: {
        name: event.target.name.value,
      },
    });

    if (error) {
      setSaving(false);
      dispatch(
        userSlice.actions.setToastPopUp({
          show: true,
          toastType: "error",
          message: error.message,
        }),
      );
      return;
    }

    if (!paymentMethod) {
      setSaving(false);
      dispatch(
        userSlice.actions.setToastPopUp({
          show: true,
          toastType: "error",
          message: "Failed to create payment method.",
        }),
      );
      return;
    }

    const data = {
      paymentId: paymentMethod.id,
      customer_id: customerId,
      subscription_id: subscriptionId,
      planCode,
      isExpired,
      createInvoice,
      addon,
    };

    try {
      const response = await axiosPost("/payments/attachCardAndUpdate", data);
      if (response.data) {
        console.log("Payment method attached successfully!");
        handleClose();
        dispatch(
          userSlice.actions.setToastPopUp({
            show: true,
            toastType: "success",
            message: response.data.message,
          }),
        );
      } else {
        console.error("Failed to attach payment method.");
        dispatch(
          userSlice.actions.setToastPopUp({
            show: true,
            toastType: "error",
            message: "",
          }),
        );
      }
    } catch (err) {
      dispatch(
        userSlice.actions.setToastPopUp({
          show: true,
          toastType: "error",
          message: "Something went wrong!",
        }),
      );
    } finally {
      setSaving(false);
    }
  };

  return (
    <div className="modal-overlay checkout_modal">
      <div className="modal-container fx al-cnt p-0">
        <form onSubmit={handleSubmit} className="h-100 w-6/12 p-6">
          <div className="form-group">
            <label htmlFor="name" className="input-label asterisk">
              Card holder
            </label>
            <input
              className="rightSidebar-tarea"
              id="name"
              type="text"
              name="name"
              placeholder="Name"
              required
            />
          </div>
          <div className="form-group">
            <label htmlFor="card-element" className="input-label asterisk">
              Credit or debit card
            </label>
            <div className="card-element-container rightSidebar-tarea">
              <CardElement options={CARD_OPTIONS} />
            </div>
          </div>
          <div className="form-group">
            <label htmlFor="name" className="input-label">
              ZIP/Postal Code
            </label>
            <input
              className="rightSidebar-tarea"
              id="zip"
              type="text"
              name="zip"
              placeholder="Enter ZIP/Postal Code"
            />
          </div>
          {/* {errorMessage && <div role="alert">{errorMessage}</div>} */}
          <div className="form-group pt-5">
            <button
              className="pay_btn"
              type="submit"
              disabled={saving || !stripe}
            >
              {saving ? "Processing..." : `Pay $${price}`}
            </button>
          </div>
          <div
            className="close new-modal-close flex-cnt og-close"
            onClick={handleClose}
          >
            <svg
              width="10"
              height="10"
              viewBox="0 0 10 10"
              fill="#6D6D6D"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                d="M10 1.00714L8.99286 0L5 3.99286L1.00714 0L0 1.00714L3.99286 5L0 8.99286L1.00714 10L5 6.00714L8.99286 10L10 8.99286L6.00714 5L10 1.00714Z"
                fill="#6D6D6D"
              />
            </svg>
          </div>
        </form>
        <div className="checkout_content h-100 w-6/12 p-6">
          <div className="grid-cnt gap-20">
            <p className="fx-d-col flex-cnt gap-8 text-center">
              <img
                src="https://dlvkyia8i4zmz.cloudfront.net/ZTHvnJXhRZujP3VnA00v_telephone_keypad_with_ten_keys.png"
                alt="reviews"
              />
              Join our growth focused community on Omniengage
            </p>
            <p className="fx-d-col flex-cnt gap-8 text-center">
              <img
                src="https://dlvkyia8i4zmz.cloudfront.net/noCrhwFqS2GdlA9Qeeep_verified.png"
                alt="secure"
              />
              Secure payment gateway with over $100 billion in global online
              transactions
            </p>
          </div>
        </div>
      </div>
    </div>
  );
};

export default CheckoutForm;
